*{margin:0;padding:0;list-style-type:none;font-family:quicksand;color:#553d3b;box-sizing:border-box;word-wrap:break-word;text-decoration:none}::selection{background-color:rgba(109,76,72,.6666666667);color:#fff}body{max-width:1200px;margin:auto;background-color:#f9f9f9}#root{margin:auto;display:flex;flex-direction:column}.pagesWrap{display:flex;justify-content:space-between;align-items:center;gap:60px}.citationWrap{max-width:600px;border-radius:20px;box-shadow:4px 4px 4px rgba(109,76,72,.6666666667),-4px -4px 4px #fff;transform:scale(1);transition:all 1s}.citationWrap:hover{box-shadow:none;cursor:pointer;transform:scale(0.99)}.citationWrap__title{border-radius:20px 20px 0 0;background-color:#f2e9e4;padding:20px 10px}.citation{text-align:justify;font-size:1.5rem}.citation__para:first-child{margin:30px;text-indent:30px}.citation__para:first-child:before:after{font-size:1.5rem;font-weight:bold}.citation__para:first-child:before{content:"« "}.citation__para:first-child:after{content:" »"}.citation__para:last-child{text-align:center;padding:30px;background-color:#f2e9e4;border-radius:0 0 20px 20px;color:rgba(109,76,72,.6666666667)}.carouselWrap{background-color:#f2e9e4;padding:60px;border-radius:50%}